import React, { Component } from 'react';
import styled from 'styled-components';

// -- json data
import CalloutImageJson from '../../static/json/map_callouts.json';

const CalloutViewerWrapper = styled.div`
	background: linear-gradient(120deg, #44505f, #19232e);
	color: #fff;
	padding: 30px;
	display: flex;
	justify-content: center;
	align-items: center;
	> div {
		min-width: 80%;
		min-height: 80%;
		background: red;
	}
`;

const ViewerInformation = styled.div``;

const CalloutImage = styled.div``;

class callout_viewer extends Component {
	render() {
		return (
			<CalloutViewerWrapper>
				<div>
					<ViewerInformation>
						<span className="map_name">
							{this.props.activeMap ? this.props.activeMap : 'No map selected'}
						</span>
						<span className="showing_info">Showing 1 of 12</span>
					</ViewerInformation>
					<CalloutImage>
						{CalloutImageJson.map(item => {
							if (item.map_name === this.props.activeMap) {
								<h1>{item.full_map_name}</h1>;
							}
						})}
					</CalloutImage>
				</div>
			</CalloutViewerWrapper>
		);
	}
}

export default callout_viewer;
